  import React, { useState } from 'react'
  import { Document, Page, pdfjs} from 'react-pdf';
  import styled from 'styled-components'

  import { SizeMe } from 'react-sizeme'

  import GlobalWrapper from 'components/global-wrapper'
  import media from 'utils/media-queries'
  import Link from 'gatsby-link'


  import Header from 'components/article/header'
  import CoverImage from 'components/article/coverimage'
  import ContentWrapper from 'components/article/contentwrapper'
  import Footer from 'components/footer'
  import FullSizeImage from 'components/article/fullsizeimage'
  import LargeImage from 'components/article/largeimage'

  import CoverSrc from 'img/articles/comp/archipix/00_archipix_intro.png'
  import ContentPDF from 'img/articles/comp/archipix/02_archiprix_citypositive.pdf'

  pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

  const Section = styled.section`
    margin: 64px 0;
    ${media.sm`
      margin: 0;
    `}
  `

  const Content = styled.div`
    canvas {
      width: 100% !important;
      height: auto !important;
    }
    max-width: 100%;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    p + h3 {
      margin-top: 42px;
    }
    padding: 0 0 0 0;
    ${media.lg`
      padding-right: 0;
    `}
    ${media.xs`
    max-width: 700px;
    padding: 0 0 0 0;
    `}
  `

  const EmbedContainer = styled.div`
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
    width: 100%;
    height: auto;
    margin-top: 32px;
    margin-bottom: 32px;
    @media (min-width: 1000px) {
      width: 1000px;
      height: 562px;
      padding-bottom: 0px;
      margin-left: auto;
      margin-right: auto;
    }
  `

  const Video = styled.iframe`
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  `

  const ImageGrid = styled.div`
    background: #f2f2f2;
    max-width: 100%;
    padding: 40px;
    margin: 32px 0;
    text-align: center;
    ${media.lg`
      padding: 32px;
      margin: 0 0 24px 0;
    `}
    ${media.md`
      margin: 0;
      margin: 0 0 16px 0;
    `}
    ${media.sm`
      padding: 16px;
    `}
  `

  const Img = styled.img`
    max-width: calc(100% - 96px);
    max-height: 300px;
    margin: 8px;
    display: inline-block;
    ${media.xl`
      max-height: 240px;
    `}
    ${media.lg`
      max-height: 200px;
    `}
    ${media.md`
      max-height: 120px;
      margin: 4px;
    `}
    ${media.sm`
      max-height: 80px;
      margin: 2px;
    `}
  `

  const LifeTime = () => {
    const [numPages, setNumPages] = useState(null);
    return (
      <GlobalWrapper>
        <Header title="The City positive: Finding order in chaos" lead="Archiprix 2017 workshop" />
        <CoverImage src={CoverSrc} focusX={'20%'} focusY={'100%'} />
        <Section>
          <ContentWrapper>
            <h2>The City Positive</h2>
            <p><b>Team //</b> Ali Karimi (Afghanistan), Ekta Rakholiya (India), Farhad Alavi (Afghanistan), 
            Franklin Gaspar (Brazil), Gert-Jan Wisse (Netherland), Giulia Fillipone (Italy), 
            Mariana Casas (Mexico), Norman Wei (New Zealand), Ridhi Panchal (India), Udit Goyal (India).</p>
            <p><b>Mentor //</b> Chitra Vishwanath </p>
            <p>
            <i>Selected for the the 11ª São Paulo Bienal de Arquitetura 2017. 
            Exhibited during the Event. Conceptualised, developed and presented during 
            Archiprix 2017 workshop at CEPT University, Ahmedabad (India).</i>
            </p>
            <p>
            The <b>nine days workshop of “City Positive”</b> was a Kaleidoscope of ideas as different 
            people from different backgrounds joined together to bring their own set of resolutions 
            to the complex situation of Ahmedabad. Aim of the workshop was to explore the imagination 
            of future cities in the present scenario. As the first stage of the workshop <b>intended to 
            study “Tyranny of small decisions”</b>, we were asked to read a few articles and books 
            that provide a holistic view of global as well as Indian scenario in the building industry. 
            This quick exercise helped us to formulate a line of direction for the upcoming workshop.
            </p>
            <p>
            First two days of the workshop were spent getting to know members of the groups and to 
            recognize the gist of the city and the group dynamics amongst us, we <b>visited three different 
            places with strikingly different characteristics</b>. Study of these 3 locations – old town 
            Manek Chowk – Hollywood Slum – and LIC housing co-operative – we learnt that decisions have 
            led to a certain way of life, social interaction, inclusiveness, use of space, sense of 
            community, ecological footprint.
            </p>
          </ContentWrapper>
        </Section>
        <Content>
        <SizeMe
          monitorHeight
          refreshRate={128}
          refreshMode={"debounce"}
          render={({ size }) => (
          <div>
            <Document 
              file={ContentPDF} 
              onLoadSuccess={({ numPages })=>setNumPages(numPages)}>
              {Array.apply(null, Array(numPages))
                  .map((x, i)=>i+1)
                  .map(page => <Page pageNumber={page} width={size.width} renderMode="png" scale={1.0}/>)}
            </Document>
          </div>
        )} />
        </Content>
        <Footer />
      </GlobalWrapper>
    )
  }

  export default LifeTime
